import tabBar from "@/components/mbbo/homeTabBar/tab_bar.vue";
import myTitle from "./news-list/news-title.vue";
import Unread from "./news-list/news-Unread.vue";
import classification from "./news-list/news-classification.vue";
import Message from "./news-list/news-Message.vue";
import {
	mapMutations,
	mapGetters
} from 'vuex';
export default {
	data() {
		return {
			list:[],
			num:[],
		}
	},
	components: {
		myTitle,
		Unread,
		classification,
		Message
	},
	created() {
		// 消息列表
		this.Message();
		//消息数量
		this.Unread();
	},
	computed: {
		...mapGetters(['GET_NEWS'])
	},
	methods:{
		// 消息列表
		Message(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/msg/selectList?userId=760&page=1', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content.list
						this.$store.commit('setnews', this.list);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		Unread(){//消息数量
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				// let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/notice/index?userId=760', //接口地址。
					// data: params,
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.num = response.data.content
						this.$store.commit('setnum', this.num);
						// this.lists = response.data.content.instKeys
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		
	},
}
